.App {
  text-align: center;
    height: 550px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


@media only screen and (max-width:600px)  {
  body{
   height: 500px;
  }

 .container{
   max-width: 230px;
   min-height: 110px;
   text-align: center;
 }

 .container #quote{
   min-height: 100px;
   margin-top: 20px;
 }
 
}